/* General styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f9f9f9;
}




#app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  color: #333;
}




.header {
  background-color: #000;
  color: #fff;
  text-align: center;
  font-size: 1em;
}




.main {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  background-color: #f1f1f1;
}




.recursion-tree {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 2px solid #ddd;
  position: relative;
}




.recursion-tree-info {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 0.9em;
  text-align: right;
}




/* Styles for input-container and toggle button */
.input-container {
  display: flex;
  align-items: center;
  position: relative;
}




.toggle-button {
  height: 300px;
  width: 50px;
  background-color: #d7263d;
  color: #fff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease;
  position: relative;
  z-index: 1; /* Ensures it stays on top */
}




.toggle-button:hover {
  background-color: #c41b2a;
}








.input-box {
  background-color: #f7e7e7;
  padding: 10px;
  display: flex;
  flex-direction: column;
  width: 330px; /* keeps the width flexible but constrained */
  max-height: 250px; /* sets a max height to prevent it from growing too large */
  transition: transform 0.5s ease;
  transform: translateX(0);
  position: absolute;
  left: 50px;
}






.input-box.slide-out {
  transform: translateX(-100%);
}




.input-box.slide-in {
  transform: translateX(0);
}


.input-box textarea {
  width: 250px;
  height: 200px;
  resize: none;
  margin-bottom: 10px;
  margin-top: 20px;
  padding: 5px;
  font-family: monospace;
}




.description-box {
  background-color: #f7e7e7; /* Background color */
  padding: 10px; /* Padding around the content */
  display: flex; /* Use flexbox for layout */
  flex-direction: column; /* Column direction for children */
  width: 270px; /* Fixed width */
  height: 250px; /* Set a maximum height */
  transition: max-height 0.3s ease; /* Smooth transition for height changes */
  overflow: hidden; /* Hide overflow by default */
  transform: translateX(0); /* Initial position */
  position: absolute; /* Position relative to parent */
  left: 50px; /* Position from the left */
}


.description-box.slide-out {
  transform: translateX(-100%); /* Slide out effect */
}


.description-box.slide-in {
  transform: translateX(0); /* Slide in effect */
}


.description-html {
  font-size: 14px; /* Font size */
  line-height: 1.5; /* Line height for readability */
  overflow-y: hidden; /* Hide vertical overflow initially */
  transition: max-height 0.3s ease;
  width: 250px;
  height: 200px;
  resize: none;
  margin-bottom: 10px;
  margin-top: 20px;
  padding: 5px;
  font-family: monospace;/* Smooth transition for height */
  background-color: white;
}








.run-button {
  background-color: #d7263d;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}




.run-button:hover {
  background-color: #c41b2a;
}




.input {
  display: flex;
  align-items: center;
}




.select-id {
  background-color: #f9f9f9;
  color: #333;
  width: 121px;
  margin-right: 10px;
}




.input-id {
  background-color: #f9f9f9;
  color: #333;
  width: 40px;
  margin-right: 10px;
}